import React, { useState, useEffect } from 'react'
import { Dialog, DialogTitle, DialogContent, DialogContentText, TextField, DialogActions, Button } from '@material-ui/core';
import FormFields from 'components/FormFields';
import { useSdk } from 'lib/sdkContext';
import { cancellablePromise, useStateReset, preventDefault } from 'lib/util';
import Loader from 'components/Loader';

const fields = [
    ['name', 'Nome', { required: true, placeholder: 'EX: Minha casa, Casa da vov, etc...', autoComplete: 'address-name' }],
    ['cep', 'CEP', { required: true }],
    ['rua', 'Endereo', { required: true }],
    ['numero', 'Nmero', { required: true }],
    ['complemento', 'Complemento'],
    ['bairro', 'Bairro', { required: true }],
]

export default function EditAddress({ open, onClose, loading: saving, }) {
    const [address, setAddress] = useStateReset({}, open)
    const [loading, setLoading] = useState()
    const sdk = useSdk()
    const { cep } = address

    const updateAddress = (field, value) => setAddress({ ...address, [field]: value })

    const saveAddress = async () => {
        setLoading(true)
        await sdk.account.addAddress(address).catch(console.error)
        setLoading(false)
        onClose()
    }

    useEffect(() => {
        if (!sdk.locations.formatCep(cep)) return
        setLoading(true)
        const [request, cancel] = cancellablePromise(sdk.locations.getCep(cep))
        request
            .then(foundAddress => setAddress({ ...address, ...foundAddress }))
            .catch(console.warn)
            .then(() => setLoading(false))

        return () => {
            cancel()
            setLoading(false)
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [sdk.locations.formatCep(cep)])


    return (
        <Dialog open={open} onClose={onClose} fullWidth maxWidth="sm">
            {(loading || saving) && <Loader />}
            <form onSubmit={preventDefault(saveAddress)}>
                <DialogTitle>Editar endereo</DialogTitle>
                <DialogContent>
                    <DialogContentText>Informe os dados do endereo</DialogContentText>
                    <FormFields fields={fields} values={address} onChange={updateAddress} />
                </DialogContent>
                <DialogActions>
                    <Button color="primary" variant="contained" type="submit">Salvar</Button>
                    <Button type="button" onClick={onClose}>Cancelar</Button>
                </DialogActions>
            </form>
        </Dialog>
    )
}
